import { mapState } from 'vuex'
export default {
  middleware: ['pagePermission'],
  layout: 'dashboard',
  components: {
    BlogReading: () =>
      import('@/components/dashboard/settings/blog/BlogReading'),
    BlogLayout: () => import('@/components/dashboard/settings/blog/BlogLayout'),
    BlogComments: () =>
      import('@/components/dashboard/settings/blog/BlogComments')
  },
  data() {
    return {
      items: [
        { title: this.$t('tabs.dashboard.settings.reading'), href: '#reading' },
        { title: this.$t('tabs.dashboard.settings.layout'), href: '#layout' },
        {
          title: this.$t('tabs.dashboard.settings.comments'),
          href: '#comments'
        }
      ]
    }
  },
  computed: {
    ...mapState('settings', {
      settings: (state) => state.settings
    })
  },
  methods: {}
}
